@import url(https://fonts.googleapis.com/css?family=Cutive+Mono|Montserrat:400,900&display=swap);
html{font-family:'Cutive Mono', monospace}body{margin:0}section{margin:80px 20px;margin-right:calc(20px + 50px)}section>h1{font-size:1.7em}section>h1::after{content:"_"}section article{width:calc(100vw - 20px * 2 - 50px)}@media screen and (min-width: 650px){section{margin:40px}section>h1{font-size:1.5em}section article{width:auto}}

nav{position:fixed;background-color:lightgray;right:0;top:-20px;height:50px;display:flex;align-items:center;padding:0 10px;transform:rotate(-90deg);transform-origin:bottom right;z-index:10}nav a{display:inline-block;margin:0 10px;transform:rotate(180deg);color:gray;font-size:1.1em;text-decoration:none}nav a::after{content:"_"}@media screen and (min-width: 650px){nav{right:30px;top:20px}nav a{font-size:1.3em}}

#header{margin-top:4em;margin-bottom:5em;font-family:'Montserrat', sans-serif}#header .name{color:#444;font-size:4em;font-weight:900;margin-bottom:0.3em}#header .name .first-name{color:black}

#skills .skillSection{display:inline-block;vertical-align:top;margin:20px 80px 20px 0}.skill{padding-bottom:10px;cursor:pointer}.skillName{display:inline-block;width:100px;text-align:right;margin-right:10px}.selectedSkill{background-color:yellow}.skillScore{font-size:1.4em;vertical-align:middle}.skillScore .skillDeficit{color:lightgray}

.preview{display:inline-block;position:relative;isolation:isolate;border:1px solid rgba(128,128,128,0.4);box-sizing:border-box;font-size:0;width:100%}.preview a{display:flex;align-items:center;color:gray;text-decoration:none}.preview img{width:100%;height:auto}.preview .desc{box-sizing:border-box;font-size:16px;padding:40px}.preview .desc h1{font-size:5em;margin:0}.preview .desc h2{display:none}.preview .desc::before{content:"";float:left;padding-top:100%}.preview .run{position:absolute;width:100%;height:100%;top:0;left:0;display:flex;align-items:center;justify-content:center;font-size:128px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer;background-color:white;mix-blend-mode:luminosity;opacity:0}.preview .run:hover{opacity:0.5}@media screen and (min-width: 650px){.preview{width:auto}.preview img,.preview .desc{width:500px;height:500px}.preview .desc h2{display:block}}

.source{margin:20px 0}.source a{text-decoration:none;color:black}.source .sprite{background-image:url(data:image/png;base64,);background-repeat:no-repeat;display:inline-block;vertical-align:middle;margin-right:5px}.source .sprite-CSSTricks{width:32px;height:32px;background-position:-1px -1px}.source .sprite-CodePen{width:32px;height:32px;background-position:-35px -1px}.source .sprite-GitHub{width:32px;height:32px;background-position:-1px -35px}.source .sprite-SkillShare{width:64px;height:32px;background-position:-69px -1px}

.techs li::before{content:"#";color:gray}.techs ul{list-style-type:none;margin:0;padding:0}

#portfolio article{display:inline-block;margin:50px 0 50px 0;vertical-align:top}#portfolio article .date{color:gray}#portfolio article .date::before{content:"// "}#portfolio article .info{display:inline-block;vertical-align:bottom;width:200px}@media screen and (min-width: 650px){#portfolio article{margin-right:50px;margin-bottom:80px}#portfolio article h1{max-width:500px}#portfolio article h1 span{display:inline-block}#portfolio article .info{padding-left:20px}}

.resume-block{display:inline-block;font-family:'Montserrat', sans-serif;margin:50px 80px 80px 0;width:100%}.resume-block h3{font-weight:normal}.resume-block .sub{font-size:0.8em;color:gray}.resume-block img{width:100%;cursor:pointer}@media screen and (min-width: 650px){.resume-block{width:300px}}

#resume article{display:inline-block;vertical-align:top;margin:40px 0 50px 0}#resume article h2{text-transform:uppercase}@media screen and (min-width: 650px){#resume article{margin-right:60px}}

#contact{width:100px}#contact a{display:block}#contact>div{margin-bottom:15px}#contact>div:first-of-type{background-color:lightgreen}

